import Vue from "vue";
import VueRouter from "vue-router";

// Because Vue is not global we tie Vue
// and VueRouter together manually
Vue.use(VueRouter);

// Initialize router
const router = new VueRouter({
  base: "/",
  mode: "hash",
  routes: [
    {
      path: "",
      component: () => import("./App.vue"),
      name: "app"
    },
    {
      path: "/screenshot_screen",
      component: () => import("./ScreenshotPage.vue"),
    },
    {
      path: "/screensavervideo",
      component: () => import("./ScreenSaver.vue"),
    },
    // {
    //   path: "/intro",
    //   component: () => import("./IntroVideoPage.vue"),
    // },
    // {
    //   path: "/view3d_screen",
    //   component: () => import("./ModelViewer.vue"),
    // },
    // {
    //   path: "/autodesk",
    //   component: () => import("./AutodeskViewer.vue"),
    // },
    // {
    //   path: "/sketchfab",
    //   component: () => import("./SketchfabViewer.vue"),
    // },
    // {
    //   path: "/view3d_screen2",
    //   component: () => import("./ModelViewer.vue"),
    // },
    {
      path: "/iframe",
      component: () => import("./iFramePage.vue"),
    },
    // {
    //   path: "/backend",
    //   component: () => import("./BackEnd.vue"),
    // },
    {
      path: "/feedback",
      component: () => import("./FeedbackPage.vue"),
    },
    {
      path: "/:slug",
      component: () => import("./VideoPage.vue"),
    },
    {
      path: "/*",
      component: () => import("./App.vue"),
      children: [
        {
          path: "/*",
          component: () => import("./App.vue"),
        }
      ]
    },
    
  ],
});

export default router;
