import Vue from 'vue'
import Index from './Index.vue'
import router from './router.js';
import store from './scripts/store.js';
import cms from './scripts/cms.js';

import vue3dLoader from "vue-3d-loader";
Vue.use(vue3dLoader);

// FONTAWESOME ––––––––––––––––––––––––––––
import { library } from '@fortawesome/fontawesome-svg-core'
import { faCircleXmark } from '@fortawesome/free-solid-svg-icons';
import { faHouseChimney } from '@fortawesome/free-solid-svg-icons';
import { faCircleLeft } from '@fortawesome/free-solid-svg-icons';
import { faVolumeOff } from '@fortawesome/free-solid-svg-icons';
import { faVolumeLow } from '@fortawesome/free-solid-svg-icons';
import { faVolumeTimes } from '@fortawesome/free-solid-svg-icons';
import { faVolumeHigh } from '@fortawesome/free-solid-svg-icons';
import { faPlay } from '@fortawesome/free-solid-svg-icons';
import { faPause } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

Vue.component('font-awesome-icon', FontAwesomeIcon);

library.add(faCircleXmark, faHouseChimney, faCircleLeft, faVolumeLow, faVolumeHigh, faVolumeTimes, faVolumeOff, faPlay, faPause)

// ANALYTICS ––––––––––––––––––––––––––––
import Analytics from 'analytics';
import googleAnalytics from '@analytics/google-analytics';

Vue.prototype.$analytics = Analytics({
  app: 'App',
  version: 100,
  plugins: [
    googleAnalytics({
      measurementIds: 'G-QDJWC8JH3Q',
    }),
  ]
})

// Vue.prototype.$analytics.on('page', ({ payload }) => {
//   console.log('Page Change event:', payload)
// })



// INIT ––––––––––––––––––––––––––––
Vue.prototype.destroy = Vue.prototype.$destroy;
Vue.config.productionTip = false

// This is where we determine if we use server or CSV automatically
function initialization() {
  return new Promise((resolve) => {
    Promise.all([
      cms.init(), 
      cms.initSettings()
    ]).then(() => {
      if (!Vue.prototype.$settings.use_CSV) {
        Promise.all([
          cms.serverInitVideos(), 
          cms.serverInitSettings()
        ]).then(() => {resolve()})
      }
      resolve();
    })
  })
}

initialization().then(() => {
  new Vue({
    vue3dLoader,
    store,
    router,
    render: h => h(Index),
  }).$mount('#app')
})

