

var axios = require('axios');
import Vue from "vue";

export default new class CMS {
  constructor() {

    Vue.prototype.$html = process.env.VUE_APP_HTML.split("|,").map((e) => {
      return {
        title: e.replace(/\n\s\s+/g, '').split("***")[0],
        html: e.replace(/\n\s\s+/g, '').split("***")[1]
      }
    });
  }

  async runexe() {
     axios.get("http://localhost:8081/runexe").then((res) => {
         console.log(res);
      })
  }

  async download (url, filename, directory) {
     axios({
         method: 'post',
         url: 'http://localhost:8081/download',
         data: {
             url: url,
             filename: filename,
             directory: directory
         }
       }).then((res) => {
         console.log("Successfully updated!", res);
     })
  }

  async addVideos(obj) {
     await axios.post("http://localhost:8081/api/video", obj).then(() => {
         this.initVideos();
     });
  }

  async updateVideo(obj) {
     await axios.put(`http://localhost:8081/api/video/${obj["_id"]}`, obj).then(() => {
         this.initVideos();
     });
  }

  async deleteVideo(obj) {
     await axios.delete(`http://localhost:8081/api/video/${obj["_id"]}`, obj).then(() => {
         this.initVideos();
     });
  }

  async editSettings(obj) {
      try {
          await axios.put(`http://localhost:8081/api/settings/${obj["_id"]}`, obj).then(() => {
              this.getSettings();
          });
      } catch(err) {
          await axios.post("http://localhost:8081/api/settings", obj).then((res) => {
              console.log(res);
          });
      }
    }

    async serverInitVideos() { // init from server
      return new Promise((resolve) => {
       axios.get("http://localhost:8081/api/video").then((res) => {
           Vue.prototype.$videos = res.data;   
           resolve(Vue.prototype.$videos);
        })
      })
    }
    
     async serverInitSettings() { // settings from server
         return new Promise((resolve) => {
             axios.get(`http://localhost:8081/api/settings`).then((settings) => {
                 Vue.prototype.$settings = settings.data[0];
                 resolve(settings.data[0]);
             })
         })
     }

  initSettings() { // settings from CSV
    let row0 = process.env.VUE_APP_CSV.split("\r\n")[0].split(",");
    let row1 = process.env.VUE_APP_CSV.split("\r\n")[1].split(",");
      return new Promise((fetched) => {
        Vue.prototype.$settings = {
          tilesPerPage: parseInt(row1[row0.indexOf("tilesPerPage")]) || 6,
          inactiveState: {
            enabled: row1[row0.indexOf("inactiveState.enabled")] === "TRUE" || false,
            play_random_tile:  row1[row0.indexOf("inactiveState.play_random_tile")] === "TRUE" || false,
            screensaver: row1[row0.indexOf("inactiveState.screensaver")] === "TRUE" || false,
            go_to_home_page: row1[row0.indexOf("inactiveState.go_to_home_page")] === "TRUE" || false,
            milliseconds: parseInt(row1[row0.indexOf("inactiveState.milliseconds")]) || 20000,
          },
          differentBGOnNested: row1[row0.indexOf("differentBGOnNested")] === "TRUE" || false,
          feedbackPageEnabled: row1[row0.indexOf("feedbackPageEnabled")] === "TRUE" || false,
          use_CSV: row1[row0.indexOf("use_CSV")] === "TRUE" || false,
          Background_Vids_Muted: row1[row0.indexOf("Background Vids Muted")] === "TRUE" || false,
          Screensaver_Vids_Muted: row1[row0.indexOf("Screensaver Vids Muted")] === "TRUE" || false,
          Activation_Vids_Muted: row1[row0.indexOf("Activation Vids Muted")] === "TRUE" || false,
          Border_Color: row1[row0.indexOf("Border Color")]  || undefined,
          Text_Color: row1[row0.indexOf("Text Color")] || undefined,
          Screensaver_Duration_in_Minutes: parseInt(row1[row0.indexOf("Screensaver Duration in Minutes")]) * 60000 || 60000,
        };
        fetched();
      })
  }

  init() { // init from CSV
    return new Promise((fetched) => {
        let row0 = process.env.VUE_APP_CSV
        .split("\r\n")[0].split(",");
        // console.log(row0);

        let arr =  process.env.VUE_APP_CSV
        .split("\r\n");


        Vue.prototype.$videos = arr.splice(1, process.env.VUE_APP_CSV.length)
        .map(i => {
          let rowVid = i.split(",");
          return {
            fields: {
              Title: rowVid[row0.indexOf("Title")],
              Thumbnail: [{
                url: rowVid[row0.indexOf("Thumbnail")] ? require(`../media/thumbnails/${rowVid[row0.indexOf("Thumbnail")]}`) : undefined
              }],
              Content: [{
                url: rowVid[row0.indexOf("Content")] ? require(`../media/content/${rowVid[row0.indexOf("Content")]}`) : undefined
              }],
              is_screenshot_page: rowVid[row0.indexOf("is_screenshot_page")] === "TRUE" || false,
              "3D_Content": rowVid[row0.indexOf("3D_Content")] || undefined,
              HTML: rowVid[row0.indexOf("HTML")] || undefined,
              Order: rowVid[row0.indexOf("Order")] || undefined,
              Parent: rowVid[row0.indexOf("Parent")] || undefined,
              Nested_Children: rowVid[row0.indexOf("Nested Children")].length > 0 ? rowVid[row0.indexOf("Nested Children")].split("/") : undefined,
              ID: rowVid[row0.indexOf("ID")] || undefined,
              Feedback_Video: rowVid[row0.indexOf("Feedback Videos")] ? rowVid[row0.indexOf("Feedback Videos")].split("/") : null,
              Loop_Video: rowVid[row0.indexOf("Loop_Video")] === "TRUE" || false
            }
          }
        })
        .sort((a, b) => a.order - b.order);
        
        fetched(Vue.prototype.$videos);
        return;
    })
  }

}
