
var axios = require('axios');
import Vue from "vue";
import store from "./store.js";

export default new class Analytics {
  constructor() {
  }

  analytics(obj) {
    return new Promise((resolve) => {
        this.fetchAnalytics().then((analytics) => {
            
            // if analytics is not array, it is from the store so format it
            if (!Array.isArray(analytics)) {
                let analyticsObj = analytics;
                analytics = Object.keys(analytics).map(i => {
                    return {
                        [i]: analyticsObj[i]
                    }
                });
            }
            // if event exists in the db
            if ([...analytics].map(i => i._id).indexOf(obj.event) > -1) {
                console.log("exists");
                // Establish the obj to update in the .db
                var objToPush = analytics.filter(i => i._id === obj.event)[0];
                // if property exists...

                switch(obj.event) {
                    // Needs 
                    case "howLongOnAPage": {
                        analytics.filter(i => i[obj.event])[0][obj.event][obj.property] ? 
                            objToPush[obj.event][obj.property].push(obj.result[0]) :
                            objToPush[obj.event][obj.property] = obj.result;
                        break;
                    }
                    // Increment tthe values by 1
                    default: {
                        analytics.filter(i => i[obj.event])[0][obj.event][obj.property] ? 
                            objToPush[obj.event][obj.property]++ : 
                            objToPush[obj.event][obj.property] = 1;
                    }
                }
                    
                this.updateAnalytics(objToPush);

            

            // if event is not in the database already,
            } else { 
                this.addAnalytics({
                    [obj.event]: {[obj.property]: obj.result}, _id: obj.event
                });
            }
            resolve();
        })
    })
  }

  // SERVER –––––––––––––––––––––––––––––––––––––––––––––––––––––––––––––––––––––
  fetchAnalytics() {
    return new Promise((resolve) => {
        if (Vue.prototype.$settings.use_CSV) {
            resolve(store.state.analytics);
            return;
        }
        axios.get("http://localhost:8081/api/analytics").then((res) => {
            resolve(res.data);
        })
    })
  }

  addAnalytics(obj) {
    return new Promise((resolve) => {
        if (Vue.prototype.$settings.use_CSV) {
            store.commit("setAnalytics", obj);
            resolve();
            return;
        }
        axios.post("http://localhost:8081/api/analytics", obj).then((res) => {
            resolve(res);
        })
    })
  }

  updateAnalytics(obj) {
    return new Promise((resolve) => {
        if (Vue.prototype.$settings.use_CSV) {
            console.log("update ", obj);
            resolve();
            return;
        }
        axios.put(`http://localhost:8081/api/analytics/${obj["_id"]}`, obj).then((res) => {
            resolve(res);
        })
    })
  }
  wipeAnalytics() {
    axios.post("http://localhost:8081/wipteanalyticsdb").then((res) => {
        resolve(res);
    })
  }
}

