<template>
  <div id="app" class="index">
      <keep-alive :max="1">
        <router-view 
        :key="$route.fullPath"
        :clickedTiles="clickedTiles"
        :route="route"
        :tilesPerPage="tilesPerPage"
        :differentBGOnNested="differentBGOnNested"
        :inactiveState="inactiveState"
        :feedbackPageEnabled="feedbackPageEnabled">
      </router-view>
      </keep-alive>
  </div>
</template>

<script>
  import {mapState} from 'vuex';
import Analytics from "./scripts/analytics.js";
  export default {
    name: "IndexI",
    components: {
    },
    watch: {
      $route: {
        handler(to,from) {
          this.isBackendPage = window.location.href.includes("backend");
          this.route = to;
          // If going to nested page 
          if (to.fullPath.includes("nestedpage")) {
              this.$store.commit("setCarouselPos", 0);
              this.$store.commit("setCarouselTransform", 0);
          }
          // // Do analytics per page
          // this.$analytics.page();

          // If you are landing on the main page, log and output that time difference you sayed on the page before you hit back button
          if (window.location.href.includes("#index.html") && from.fullPath !== "/" ) {
              this.$analytics.track({event: "backButtonTime", property: from.fullPath, result: performance.now() - this.timeEnteredOnAnyPage}, (e) => {
                Analytics.analytics(e.payload.properties);
                // this.$store.commit("setAnalytics",e.payload.properties);
                // console.log(`Back button time from ${from.fullPath} `, e.payload.properties);
              });
          }

          

          this.timeEnteredOnAnyPage = performance.now();

          this.$nextTick(() => {
            if (document.getElementById("modelviewer") || document.getElementById("screenshotpage")) {
                this.$analytics.track({event: "screenshot_or_3dcontent_page_loaded", description: "Number of times a Hologram Content / 3D content / Screenshot etc. page loads", loaded: this.$analytics.screenshot_or_3dcontent_page_loaded}, (e) => {
                  this.$store.commit("analyticsIncrement", "screenshot_or_3dcontent_page_loaded");
                });
              }
            }
          );

        }
      },
      carouselPos: {
        handler() {
          // Track and output the number of times a certain carousel is loaded
          this.loadedSlides[this.carouselPos]++;
          // Number of times a given Slide in the Carousel is loaded
          this.$analytics.track({event: "carouselPos", property: `Slide ${this.carouselPos}`, result: `Loaded`}, (e) => {
            // Analytics.write("slideLoaded", {slide: this.carouselPos, loaded: this.loadedSlides[this.carouselPos]});
            this.$store.commit("setAnalytics",e.payload.properties);
          });
        }
      }
    },
    computed: {
      ...mapState(['carouselTransform', 'carouselPos','activeDirectory', 'homeDirectory', 'activePage', 'cacheCleared'])
    },
    methods: {

    },
    data() {
        return {
          clickedTiles: [],
          loadedSlides: [],
          route: null,
          timeEnteredOnPage: 0,
          timeEnteredOnAnyPage: 0,
          screenshot_or_3dcontent_page_loaded: 0,
          isBackendPage: false,
          introPagePlayedVar: false,
          tilesPerPage:  this.$settings.tilesPerPage,
          // tilesPerPage:  3,
          differentBGOnNested: this.$settings.differentBGOnNested,
          inactiveState: this.$settings.inactiveState,
          feedbackPageEnabled: this.$settings.feedbackPageEnabled,
        }
    },

    mounted() {
      if (this.activePage === "ScreensaverPage") {
        this.$router.push("screensavervideo");
      }
      // this.$store.commit("setCarouselPos", 0);
      // this.$store.commit("setCarouselTransform", 0);

      // this.$store.commit('setNestedRank',0);
      // window.location.href = "/";
      // this.$router.push("/");

      if (!this.cacheCleared) {
        // empty the array by default, then populate with top-level tiles 
        this.$store.commit("setActiveDirectory", null);
        this.$store.commit("setActiveDirectory", [...this.$videos].filter(i => i.fields.Parent === undefined));
        this.$store.commit('setCacheCleared', true);
      }
      this.$store.commit("setHomeDirectory", [...this.$videos].filter(i => i.fields.Parent === undefined));

      // Documents how many times a certain tile is clicked. Upon refresh, this array is populated with 0
      this.clickedTiles = this.$videos.map(() => 0);
      this.$store.commit('setClickedTiles', this.clickedTiles);
      // How many slides there should be, set the counter array
      var slideCount = Math.ceil(this.clickedTiles.length / this.tilesPerPage);
      this.loadedSlides.length = slideCount;
      this.loadedSlides = [...this.loadedSlides].map(() => 0);

      // This extra variable prevents the show-before-refresh of the activation screen after the attraction
      this.$nextTick(() => {
        this.introPagePlayedVar = this.activePage;
      })
    },

  };
</script>

<style>

  @import "./css/style.css";
</style>
