import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
import createPersistedState from "vuex-persistedstate";

const initialState = () => ({
  carouselInfo: 0,
  carouselTransform: 0,
  carouselPos: 0,
  // carouselPos: [],

  clickedTilesArray:[],
  activeDirectory: [],
  homeDirectory: '',
  nestedRank: 0,
  analytics: {
    howLongOnAPage: [],
    backButtonTime:[],
    screenshot_or_3dcontent_page_loaded: 0,
    timesIdle: 0,
    tileClick:[],
    carouselPos: [],
  },
  activePage: "App",
  cacheCleared: false,
  feedback: [
    // {
    //   video: "AZ1",
    //   response: {
    //     happy: 0,
    //     love: 0,
    //     neutral: 0,
    //     wow: 0
    //   }
    // }
  ]
})

export default new Vuex.Store({
  state: initialState,

  mutations: {
    // Remembers the last carousel position the user was on
    setCarouselTransform(state, carouselTransform) {
      Vue.set(state, "carouselTransform", carouselTransform);
    },
    setCarouselPos(state, carouselPos) {
      Vue.set(state, "carouselPos", carouselPos);
    },
    setClickedTiles(state, clickedTilesArray) {
      // Vue.set(state, "clickedTilesArray", clickedTilesArray);
      state.clickedTilesArray = clickedTilesArray;
    },
    setActiveDirectory(state, dir) {
      if (!dir) {
        state.activeDirectory = [];
      } else {
        state.activeDirectory.push(dir);
      }
      Vue.set(state, "activeDirectory", state.activeDirectory);
    },
    setHomeDirectory(state, homeDirectory) {
      Vue.set(state, "homeDirectory", homeDirectory);
    },
    setNestedRank(state, nestedRank) {
      Vue.set(state, "nestedRank", nestedRank);
    },
    goBack(state) {
      state.activeDirectory.pop();
    },
    reset(state) {
      const initial = initialState();
      Object.keys(initial).forEach(key => { state[key] = initial[key] });
    },
    setAnalytics(state, obj) {
      try {
        state.analytics[obj._id].push(obj);
      } catch {
        // idk yet
      }
    },
    setAnalyticsIncrementally(state, obj) {
      state.analytics[obj.event] = obj.property;
    },
    resetAnalytics(state) {
      state.analytics = {};
      state.howLongOnAPage = [];
      state.backButtonTime = [],
      state.introPagePlayed = false;
      state.cacheCleared = false;
      state.carouselPos = 0;
      state.carouselTransform = 0;
      state.feedback = [];
    },
    setActivePage(state, page) {
      state.activePage = page;
    },
    analyticsIncrement(state, property) {
      console.log(state.analytics);
      state.analytics[property]++;
    },
    setCacheCleared(state, cacheCleared) {
      state.cacheCleared = cacheCleared;
    },
    setFeedback(state, feedback) {
      state.feedback = feedback;
    }
  },

  actions: {
    setCarouselTransform(context, carouselTransform) {
      context.commit("setCarouselTransform", carouselTransform);
    },
    setCarouselPos(context, carouselPos) {
      context.commit("setCarouselPos", carouselPos);
    },
    setClickedTiles(context, clickedTilesArray) {
      context.commit("setClickedTiles", clickedTilesArray);
    },
    setActiveDirectory(context, activeDirectory) {
      context.commit("setActiveDirectory", activeDirectory);
    },
    setHomeDirectory(context, homeDirectory) {
      context.commit("setHomeDirectory", homeDirectory);
    },
    setNestedRank(context, nestedRank) {
      context.commit("setNestedRank", nestedRank);
    },
    goBack(context) {
      context.commit("goBack");
    },
    resetAnalytics(context) {
      context.commit("resetAnalytics");
    }
  },
  plugins: [createPersistedState()],
});
